<template>
  <section>
    <topbar>
      <template slot="before">
        <b class="iconfont icon-back" @click="$router.go(-1)"></b>
      </template>
      俱乐部详情
    </topbar>
    <section class="preview">
      <div class="cover">
        <img :src="club.cover"/>
      </div>
      <div class="text">
        <h3>{{club.title}}</h3>
        <p>{{club.summary}}</p>
      </div>
      <div class="ft">
        <template v-if="isJoin">
          <button class="ui-button disabled">已加入俱乐部</button>
        </template>
        <template v-else>
          <button class="ui-button primary" @click="handJoin">加入俱乐部</button>
        </template>
      </div>
    </section>
    <section class="detail">
      <tab :select.sync="tab.select" :data="tab.list"></tab>
    </section>
  </section>
</template>

<script>
  import { mapState } from 'vuex'
  import Topbar from '@/components/topbar'
  import Tab from '@/components/tab'

  export default {
    name: 'club',
    components: {
      Topbar,
      Tab
    },
    computed: {
      ...mapState({}),
      isJoin () {
        return true
      }
    },
    data () {
      return {
        club: {
          cover: 'data:image/jpeg;base64,',
          title: 'Sasa俱乐部',
          summary: 'Sasa俱乐部的简介，由Sasa成立，带领过无数次的潜水行程，给俱乐部成员带来了终生难忘的美好回忆。期待和你一起探索世界哇。'
        },
        tab: {
          select: 0,
          list: ['课程', '行程', '装备', '帖子']
        },
      }
    }
  }
</script>

<style lang="scss" scoped>
  .ui-button {
    background-color: #fff;
    padding: 6px 10px;
    border-radius: 10px;
    border-width: 0;
    &.primary {
      background-color: #1797c6;
      color: #fff;
    }
    &.disabled {
      background-color: #ccc;
      color: #fff;
    }
  }

  .preview {
    background-color: #fff;
    color: #333;
    margin-bottom: 10px;
    .text {
      padding: 0 15px;
      margin-bottom: 20px;
    }
    .cover {
      img {
        width: 100%;
        display: block;
      }
    }

    h3 {
      font-size: 22px;
      font-weight: 600;
      padding-top: 5px;
    }
    .ft {
      text-align: center;
      padding-bottom: 25px;
    }
  }

  .detail {
    /*background-color: #fff;*/
  }
</style>



